ul.main-menu {
  font-size: 20px;
  display: flex;
  list-style: none;
  margin: 0;
  border-bottom: 4px solid rgb(0, 255, 196);
}
 li.menu-item {
  padding: 20px;
 }
 ul.dropdown-menu {
  list-style: none;
  padding: 0 5px;
  border-left: 2px solid rgb(0, 255, 196);
  border-right: 2px solid rgb(0, 255, 196);
  border-bottom: 2px solid rgb(0, 255, 196);
 }
 .sub-menu {
  display: none;
 }
 li.menu-item.menu-item2:hover  .sub-menu{
  display: block;
  position: absolute;
  padding-top: 23px;
 }
 li.sub-item {
  padding: 10px 0;
 }
 li a {
  text-decoration: none;
  color: black;
 }
 li a:hover {
  color:rgb(0, 255, 196);
 }
 
